@media only screen and (max-width: 800px) {

  .wrapper {
    display: grid;
    /* background-color: rgba(0, 0, 0, 0.5); */
    background-image: url('/city_backdrop.jpg');
    background-size: cover;
    /* opacity: 0.1; */
    color: rgb(255, 255, 255);
    width: 100%;
    height: 700px;
    grid-template-areas:  "fav"
                          "icon"
                          "location"
                          "forecast"
                          "forecastdate"
                          "forecastcard"
                          "forecastcard"
                          "forecastcard"
                          "forecastcard"
                          "forecastcard" !important;
    grid-template-rows: 1fr 1fr 1fr  1fr 1fr 1fr 1fr 1fr 1fr 1fr !important;
    grid-template-columns: auto !important;
  }
 .fav{
  justify-self: center !important;
 }
  .header{
    flex-direction: column !important;
  }
  .forecast-card-holder{
    flex-direction: column !important;
  }
  .location{
    height: auto !important;
  }
  .search-bar{
    width: 200px !important;
  }
}


.favorite-button-enter {
    opacity: 0.01;
  }
 
  .favorite-button-enter.favorite-button-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
  }

 .favorite-button-exit {
    opacity: 1;
  }
 

 .favorite-button-exit.favorite-button-exit-active {
    opacity: 0.01;
    transition: opacity 300ms ease-in;
  }
.wrapper {
  display: grid;
  /* background-color: rgba(0, 0, 0, 0.5); */
  background-image: url('/city_backdrop1.jpg');
  background-size: unset;
  background-position-y: center;
  background-position-x: right;

  color: rgb(255, 255, 255);
  width: 100%;
  height: 700px;
  grid-template-areas:".                 icon                 icon               icon                       fav"
                        "location      .               forecast            .                   forecastdate"
                        "forecastcard     forecastcard     forecastcard     forecastcard      forecastcard" ;
  grid-template-rows: 20% 40% 40%;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}

.main-card-enter {
  opacity: 0.01;
}

.main-card-enter.main-card-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}

.main-card-exit {
  opacity: 1;
}


.main-card-exit.main-card-exit-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}

.forecast-card-holder{
 grid-area: forecastcard;
 display: flex;
 flex-direction: row;
  align-items: flex-end;
}

.icon{
  grid-area: icon;
  display: flex;
  justify-content: center;
  align-items: center;
  place-self: inherit;
}

.fav{
  grid-area: fav;
  place-self: end;
  display: flex;
  justify-content: center;
  align-items: center;
}

.forecast-date{
  grid-area: forecastdate;
  place-self: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.forecast-temp{
  grid-area: forecast;
  place-self: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.location{
  grid-area: location;
  place-self: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
